.wrapper {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    display: flex;
    height: 0;
    opacity: 0;
    transform: translateY(-8px);
    align-items: center;
    justify-content: center;
    border-radius: 0 0 8px 8px;
    background-color: var(--P-background-color);
    padding: 0 18px;
    transition: all 0.2s;
    z-index: -1;

    &.show {
        height: 34px;
        opacity: 1;
        transform: translateY(0px);
        z-index: 1;
    }
}

.range-track {
    position: absolute;
    display: flex;
    width: calc(100% - 36px);
    background-color: rgb(255 255 255 / 5%);
    height: 6px;
    border-radius: 100px;
    overflow: hidden;

    .track {
        background-color: var(--primary-color);
        width: 50px;
    }
}

.input-range {
    position: absolute;
    -webkit-appearance: none;
    background-color: transparent;
    height: 6px;
    width: calc(100% - 36px);
    border-radius: 100px;

    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        height: 16px;
        width: 16px;
        border-radius: 1000px;
        z-index: 10;
        background: var(--primary-color);
    }
}
